<template> 
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float:right"
          type="primary"
          @click="handleSearchList()"
          size="small">
          查询搜索
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 25px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="80px">
          <el-form-item label="订单号：">
            <el-input v-model="listQuery.orderSn" class="input-width" placeholder="订单号" clearable></el-input>
          </el-form-item>
          <el-form-item label="购买方：">
            <el-input v-model="listQuery.customerName" class="input-width" placeholder="购买方" clearable></el-input>
          </el-form-item>
          <el-form-item label="日期：">
            <el-date-picker
              v-model="listQuery.orderDate"
              type="daterange"
              align="right"
              unlink-panels
              range-separator="~"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
<!--      <el-button size="mini" class="btn-add" @click="handleAddCustomer()" style="margin-left: 20px">添加</el-button>-->
    </el-card>
    <div class="table-container">
      <el-table ref="roleTable"
                :data="list"
                style="width: 100%;"
                v-loading="listLoading" border>
        <el-table-column label="订单号" align="center">
          <template slot-scope="scope">{{scope.row.orderSn}}</template>
        </el-table-column>
        <el-table-column label="购买方" align="center">
          <template slot-scope="scope">{{scope.row.customerName}}</template>
        </el-table-column>
        <el-table-column label="日期" align="center">
          <template slot-scope="scope">{{scope.row.orderDate | formatDateTime}}</template>
        </el-table-column>
        <el-table-column label="合计" align="center">
          <template slot-scope="scope">￥{{scope.row.totalAmount }}</template>
        </el-table-column>
        <el-table-column label="备注" align="center">
          <template slot-scope="scope">{{scope.row.orderOther }}</template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleOrderDetail(scope.$index, scope.row)">查看详情
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination-container">
      <el-pagination

        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :current-page.sync="listQuery.pageNum"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      title="订单详情"
      :visible.sync="dialogVisible"
      :top = "'10vh'"
      width="60%">
      <el-card shadow="never" id="orderCard" ref="orderCard" style="">
        <div slot="header">
          <!--          <svg-icon icon-class="marker" style="color: #606266"></svg-icon>-->
          <span class="font-small">订单</span>
        </div>
        <el-form :model="order" label-position="left" label-width="10%" style="width: 100%;" size="small">
          <el-form-item label="订单号：" prop="orderSn">
            <el-row> {{order.orderSn}} </el-row>
          </el-form-item>
          <el-form-item label="购买方：" prop="customerName">
            <el-row> {{order.customerName}} </el-row>
          </el-form-item>
          <el-form-item label="日期：" prop="orderDate">
            <el-row> {{order.orderDate | formatDateTime}} </el-row>
          </el-form-item>
          <el-form-item label="备注：" prop="orderOther">
            <el-row> {{order.orderOther}} </el-row>
          </el-form-item>
        </el-form>
        <el-table
          ref="orderItemTable"
          :data="order.goodList"
          style="width: 100%;margin-top: 20px">
          <el-table-column label="供货商" width="120" align="center">
            <template slot-scope="scope">{{scope.row.firm}}</template>
          </el-table-column>
          <el-table-column label="货品名称" align="center">
            <template slot-scope="scope">{{scope.row.stock}}</template>
          </el-table-column>
          <el-table-column label="规格型号" align="center">
            <template slot-scope="scope">{{scope.row.model}}</template>
          </el-table-column>
          <el-table-column label="数量" align="center">
            <template slot-scope="scope">{{scope.row.num}}</template>
          </el-table-column>
          <el-table-column label="单价" align="center">
            <template slot-scope="scope">{{scope.row.price}}</template>
          </el-table-column>
          <el-table-column label="小计" align="center">
            <template slot-scope="scope">
              ￥{{scope.row.num*scope.row.price}}
            </template>
          </el-table-column>
        </el-table>
        <div style="float: right;margin: 20px">
          合计：<span class="color-danger">￥{{order.totalAmount}}</span>
        </div>
      </el-card>
    </el-dialog>
  </div>
</template>
<script>
  import {formatDate} from '@/utils/date';
  import {delCustomer, fetchCustomerList, updateCustomer} from "@/api/customer";
  import {fetchOrderList} from "@/api/order";

  const defaultListQuery = {
    pageNum: 1,
    pageSize: 5,
    orderSn: null,
    customerName: null,
    orderDate: null
  };
  export default {
    name: 'orderList',
    data() {
      return {
        listQuery: Object.assign({}, defaultListQuery),
        list: null,
        total: null,
        listLoading: false,
        dialogVisible: false,
        rules: {
          customerName:  [{required: true, message: '请输入', trigger: 'blur'}],
          customerTel: [{required: true, message: '请输入', trigger: 'blur'}],
          other: [{required: false, message: '请输入', trigger: 'blur'}],
        },
        order: {}
      }
    },
    created() {
      this.getOrderList();
    },
    filters: {
      formatDateTime(time) {
        if (time == null || time === '') {
          return 'N/A';
        }
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd')
      }
    },
    methods: {
      handleResetSearch() {
        this.listQuery = Object.assign({}, defaultListQuery);
      },
      handleSearchList() {
        this.listQuery.pageNum = 1;
        this.getOrderList();
      },
      handleSizeChange(val) {
        this.listQuery.pageNum = 1;
        this.listQuery.pageSize = val;
        this.getOrderList();
      },
      handleCurrentChange(val) {
        this.listQuery.pageNum = val;
        this.getOrderList();
      },

      handleOrderDetail(index,row){
        this.order = Object.assign({}, row);
        this.dialogVisible=true;
      },
      getOrderList() {
        this.listLoading = true;
        fetchOrderList(this.listQuery).then(response => {
          this.list = response.data.list;
          this.total = response.data.total;
          this.listLoading = false;
        });
      },
    }
  }
</script>
<style></style>





